/* eslint-disable cypress/no-unnecessary-waiting */
import React, { FC } from 'react';
import { mount, CypressTestDecorator, getComponent } from '@sberdevices/plasma-cy-utils';

import { ProductCard, GalleryCard, MusicCard } from '../Card/Card.examples';

import { ScalingColCard, scaleCallback, scaleResetCallback } from './Carousel.examples';

import { useRemoteHandlers } from '.';

const image =
    'data:image/jpeg;base64,';

const items = [
    { title: 'Каждый', subtitle: 'Each', imageSrc: image },
    { title: 'Охотник', subtitle: 'Hunter', imageSrc: image },
    { title: 'Желает', subtitle: 'Whants', imageSrc: image },
    { title: 'Знать', subtitle: 'To know', imageSrc: image },
    { title: 'Где', subtitle: 'Where', imageSrc: image },
    { title: 'Сидит', subtitle: 'Is', imageSrc: image },
    { title: 'Фазан', subtitle: 'The pheasant', imageSrc: image },
];

describe('plasma-ui: Carousel', () => {
    const Container = getComponent('Container');
    const Row = getComponent('Row');
    const Carousel = getComponent('Carousel');
    const CarouselGridWrapper = getComponent('CarouselGridWrapper');
    const CarouselCol = getComponent('CarouselCol');
    const CarouselItem = getComponent('CarouselItem');
    const Body3 = getComponent('Body3');

    const CarouselDecorator: FC = ({ children }) => (
        <CypressTestDecorator>
            <Container>{children}</Container>
        </CypressTestDecorator>
    );

    const AnimatedCarouselX: FC = () => {
        const [index, setIndex] = useRemoteHandlers({
            initialIndex: 0,
            axis: 'x',
            delay: 30,
            longDelay: 150,
            min: 0,
            max: items.length - 1,
        });

        return (
            <CarouselGridWrapper>
                <Carousel
                    as={Row}
                    axis="x"
                    index={index}
                    detectActive
                    detectThreshold={0.5}
                    animatedScrollByIndex
                    onIndexChange={(i) => setIndex(i)}
                    scrollSnapType="none"
                    style={{ paddingTop: '1.25rem', paddingBottom: '1.25rem' }}
                >
                    {items.map(({ title, subtitle, imageSrc }, i) => (
                        <CarouselCol key={`item:${i}`} size={3} sizeXL={4}>
                            <ProductCard title={title} subtitle={subtitle} focused={i === index} imageSrc={imageSrc} />
                        </CarouselCol>
                    ))}
                </Carousel>
            </CarouselGridWrapper>
        );
    };

    const AnimatedCarouselY: FC = () => {
        const [index, setIndex] = useRemoteHandlers({
            initialIndex: 0,
            axis: 'y',
            delay: 30,
            longDelay: 150,
            min: 0,
            max: items.length - 1,
        });

        return (
            <Carousel
                as={Row}
                axis="y"
                index={index}
                paddingStart="50%"
                paddingEnd="50%"
                detectActive
                detectThreshold={0.5}
                animatedScrollByIndex
                onIndexChange={(i) => setIndex(i)}
                scrollSnapType="none"
                style={{
                    height: '100vh',
                    maxHeight: '40rem',
                    width: '100%',
                    maxWidth: '22.5rem',
                    margin: '0 auto',
                    padding: '0.75rem',
                }}
            >
                {items.map(({ title, subtitle, imageSrc }, i) => (
                    <CarouselItem key={`item:${i}`} style={{ padding: '0.75rem 0' }}>
                        <GalleryCard
                            title={title}
                            subtitle={subtitle}
                            focused={i === index}
                            imageSrc={imageSrc}
                            imageRatio="1 / 1"
                            scaleOnFocus
                        />
                    </CarouselItem>
                ))}
            </Carousel>
        );
    };

    it('basic', () => {
        const index = 0;

        mount(
            <CarouselDecorator>
                <CarouselGridWrapper>
                    <Carousel
                        as={Row}
                        axis="x"
                        index={index}
                        scrollSnapType="none"
                        style={{ paddingTop: '1.25rem', paddingBottom: '1.25rem' }}
                    >
                        {items.map(({ title, subtitle, imageSrc }, i) => (
                            <CarouselCol key={`item:${i}`} size={3} sizeXL={4}>
                                <ProductCard
                                    title={title}
                                    subtitle={subtitle}
                                    focused={i === index}
                                    imageSrc={imageSrc}
                                />
                            </CarouselCol>
                        ))}
                    </Carousel>
                </CarouselGridWrapper>
            </CarouselDecorator>,
        );
        cy.wait(100);
        cy.matchImageSnapshot();
    });

    it('vertical', () => {
        const index = 0;

        mount(
            <CarouselDecorator>
                <Carousel
                    as={Row}
                    axis="y"
                    index={index}
                    paddingStart="50%"
                    paddingEnd="50%"
                    scrollSnapType="none"
                    style={{
                        height: '100vh',
                        maxHeight: '40rem',
                        width: '100%',
                        maxWidth: '22.5rem',
                        margin: '0 auto',
                        padding: '0.75rem',
                    }}
                >
                    {items.map(({ title, subtitle, imageSrc }, i) => (
                        <CarouselItem key={`item:${i}`} style={{ padding: '0.75rem 0' }}>
                            <GalleryCard
                                title={title}
                                subtitle={subtitle}
                                focused={i === index}
                                imageSrc={imageSrc}
                                imageRatio="1 / 1"
                                scaleOnFocus
                            />
                        </CarouselItem>
                    ))}
                </Carousel>
            </CarouselDecorator>,
        );
        cy.wait(100);
        cy.matchImageSnapshot();
    });

    it('music', () => {
        mount(
            <CarouselDecorator>
                <section style={{ margin: '1.75rem 0' }}>
                    <Body3 style={{ marginBottom: '1rem' }}>Новые альбомы</Body3>
                    <CarouselGridWrapper>
                        <Carousel as={Row} axis="x" index={0} scrollSnapType="none">
                            {items.map((item, i) => (
                                <CarouselCol key={`item:${i}`} size={2} sizeM={1.5}>
                                    <MusicCard {...item} imageRatio="1 / 1" />
                                </CarouselCol>
                            ))}
                        </Carousel>
                    </CarouselGridWrapper>
                </section>
                <section style={{ margin: '1.75rem 0' }}>
                    <Body3 style={{ marginBottom: '1rem' }}>Хиты и чарты</Body3>
                    <CarouselGridWrapper>
                        <Carousel as={Row} axis="x" index={0} scrollSnapType="none">
                            {items.map((item, i) => (
                                <CarouselCol key={`item:${i}`} size={4} sizeM={3}>
                                    <MusicCard {...item} imageRatio="16 / 9" />
                                </CarouselCol>
                            ))}
                        </Carousel>
                    </CarouselGridWrapper>
                </section>
                <section style={{ margin: '1.75rem 0' }}>
                    <Body3 style={{ marginBottom: '1rem' }}>Жанры и настроения</Body3>
                    <CarouselGridWrapper>
                        <Carousel as={Row} axis="x" index={0} scrollSnapType="none">
                            {items.map((item, i) => (
                                <CarouselCol key={`item:${i}`} size={3} sizeM={2}>
                                    <MusicCard {...item} imageRatio="16 / 9" />
                                </CarouselCol>
                            ))}
                        </Carousel>
                    </CarouselGridWrapper>
                </section>
            </CarouselDecorator>,
        );
        cy.wait(100);
        cy.matchImageSnapshot();
    });

    it('center', () => {
        const index = 0;

        mount(
            <CarouselDecorator>
                <CarouselGridWrapper>
                    <Carousel
                        as={Row}
                        axis="x"
                        index={index}
                        detectActive
                        detectThreshold={0.5}
                        scaleCallback={scaleCallback}
                        scaleResetCallback={scaleResetCallback}
                        paddingStart="50%"
                        paddingEnd="50%"
                        scrollSnapType="none"
                        style={{ paddingTop: '7.5rem' }}
                    >
                        {items.map((item, i) => (
                            <ScalingColCard key={`item:${i}`} isActive={i === index} item={item} />
                        ))}
                    </Carousel>
                </CarouselGridWrapper>
            </CarouselDecorator>,
        );

        cy.get('body').type('{rightarrow}');
        cy.wait(1000);
        cy.get('body').type('{rightarrow}');
        cy.wait(1000);
        cy.get('body').type('{leftarrow}');
        cy.wait(1000);
        cy.matchImageSnapshot();
    });

    it('_scrollAlign', () => {
        const index = 0;

        const scrollAlignList = ['start', 'center', 'end', 'activeDirection'];

        mount(
            <CarouselDecorator>
                {scrollAlignList.map((scrollAlign) => (
                    <CarouselGridWrapper>
                        <Carousel
                            as={Row}
                            axis="x"
                            index={index}
                            detectActive
                            detectThreshold={0.5}
                            scaleCallback={scaleCallback}
                            scaleResetCallback={scaleResetCallback}
                            scrollAlign={scrollAlign}
                            paddingStart="50%"
                            paddingEnd="50%"
                            scrollSnapType="none"
                            style={{ paddingTop: '7.5rem' }}
                        >
                            {items.map((item, i) => (
                                <ScalingColCard key={`item:${i}`} isActive={i === index} item={item} />
                            ))}
                        </Carousel>
                    </CarouselGridWrapper>
                ))}
            </CarouselDecorator>,
        );

        cy.matchImageSnapshot();
    });

    it('animated X', () => {
        mount(
            <CarouselDecorator>
                <AnimatedCarouselX />
            </CarouselDecorator>,
        );

        cy.get('body').type('{rightarrow}');
        cy.wait(1000);
        cy.get('body').type('{rightarrow}');
        cy.wait(1000);
        cy.get('body').type('{leftarrow}');
        cy.wait(1000);
        cy.get('body').type('{enter}');
        cy.matchImageSnapshot();
    });

    it('animated Y', () => {
        mount(
            <CarouselDecorator>
                <AnimatedCarouselY />
            </CarouselDecorator>,
        );

        cy.get('body').type('{downarrow}');
        cy.wait(1000);
        cy.get('body').type('{downarrow}');
        cy.wait(1000);
        cy.get('body').type('{uparrow}');
        cy.wait(1000);
        cy.get('body').type('{enter}');
        cy.matchImageSnapshot();
    });
});
